<template>
    
    <div id="mypage_wrap">
        <div id="menu_box">
            <ul id="menu">
                <li @click="state.mynum = 1"> </li>
                <li @click="state.mynum = 2"> </li>
                <li @click="state.mynum = 3"> </li>
                <li @click="state.mynum = 5">  </li>
                {{ state.userInfo }}
            </ul>
        </div>
        <div id="content_box">
            
            <!--   -->
            <div v-if="state.mynum === 1&&state.userInfo" class="myinf">
                <div class="myinf_wrap">
                    <div class="myinf_left">
                        <div class="myinf_con">
                            <label for="email" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.userInfo.email }}</p>
                        </div>
                        <div class="myinf_con">
                            <label for="name" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.userInfo.name }}</p>
                        </div>
                        <div class="myinf_con">
                            <label for="gender" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.userInfo.gender }}</p>
                        </div>
                        <div class="myinf_con">
                            <label for="address" class="myinf_lbl"></label>
                            <p style="float:left;margin-left:20px;" class="myinf_con_p">{{ state.userInfo.address }}</p>
                            <p>{{ state.userInfo.detailaddress }}</p>
                        </div>
                        <div>
                            <label for="progress" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.grade }}</p>
                        </div>
                        <div>
                            <label for="progress" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.mytotalreviewcount }}</p>
                        </div>
                        <div>
                            <label for="" class="myinf_lbl" style="width:190px;float:left;"> / </label>
                            <p style="float:left;margin-left:20px;">{{ state.userInfo.certreveiwcount }}/
                            {{state.totalshopcount}}</p>
                            <div class="progress" style="width:400px;float:left;">
                                <div class="progress-bar progress-bar-striped bg-info" role="progressbar"
                                    aria-label="Example with label" style="width: 50%;" aria-valuenow="25"
                                    aria-valuemin="0" aria-valuemax="100">{{ state.certpercent }}%</div>
                            </div>
                            <el-popover placement="bottom" :width="100"
                                content="> : 0 ~ 9%  : 10 ~ 29%  : 30 ~ 49%  : 50 ~ 69%   : 70 ~ 89%">
                                <template #reference>
                                    <el-button class="m-2"></el-button>
                                </template>
                            </el-popover>
                        </div>


                    </div>
                    <div class="myinf_right">
                        <div class="myinf_con">
                            <img :src="state.image" class="profileImg" alt="">
                        </div>
                    </div>
                </div>
            </div>

            <!--   -->
            <div v-if="state.mynum === 2" class="myinf">
                <div class="myinf_wrap">
                    <div class="myinf_left">
                        <div class="myinf_con">
                            <label for="email" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.userInfo.email }}</p>
                        </div>
                        <div class="myinf_con">
                            <label for="name" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.userInfo.name }}</p>
                        </div>
                        <div class="myinf_con">
                            <label for="gender" class="myinf_lbl"></label>
                            <p class="myinf_con_p">{{ state.userInfo.gender }}</p>
                        </div>
                        <div class="field">
                            <label for="address" class="myinf_lbl"></label>
                            <p style="float:left;margin-left:20px;" class="myinf_con_p">{{ state.userInfo.address }}</p>
                            <p>{{ state.userInfo.detailaddress }}</p>
                            <div style="margin-top:10px;">
                                <el-input type="text" id="sample6_postcode" v-model="state.postcode"
                                    style="width:200px;" placeholder=""></el-input>
                                <el-input type="button" @click="sample6_execDaumPostcode()" value=" "
                                    style="width:100px;"></el-input>
                            </div>
                            <el-input type="text" id="sample6_address" ref="address" v-model="state.address"
                                style="width:400px;" placeholder=""></el-input><br>
                            <el-input type="text" id="sample6_detailAddress" ref="detailaddress"
                                v-model="state.detailaddress" style="width:400px;" placeholder=""></el-input>
                            <el-input type="text" id="sample6_extraAddress" style="width:400px;"
                                placeholder=""></el-input>
                        </div>
                        <div>
                            <label for="password" class="myinf_lbl" style="float:left;"></label>
                            <el-input type="password" v-model="state.password" placeholder=" ." ref="password"
                                style="width:300px;margin-left:5px;" />
                        </div>
                        <div>
                            <label for="password1" class="myinf_lbl" style="float:left;"> </label>
                            <el-input type="password" v-model="state.password1" placeholder="  ."
                                ref="password1" style="width:300px;margin-left:5px;" />
                        </div>
                        <div>
                            <el-button @click="usermodify()"
                                style="width:100%;margin-top:20px;background: goldenrod; color:white;"></el-button>
                        </div>
                    </div>
                    <div class="myinf_right">
                        <div class="myinf_con">
                            <img :src="state.image" class="profileImg" alt="">
                            <input type="file" v-on:change="handleImage($event)" />
                        </div>
                    </div>
                </div>
            </div>

            <!--   -->
            <div v-if="state.mynum === 3" class="myinf">
                <div id="myquestion_wrap">
                    <table class="table table-hover">
                        <thead>
                            <tr>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"> </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(data, i) of state.questionRows" :key="i">
                                <th scope="row">{{ data._id }}</th>
                                <td
                                    @click="state.mynum = 4; handleOneQuestion(data._id); selectonequestionrelply(data._id);">
                                    {{ data.title }}</td>
                                <td>{{ data.writer }}</td>
                                <td>{{ data.regdate1 }}</td>
                                <td v-if="data.state === 0" style="color:red;"></td>
                                <td v-if="data.state === 1" style="color:green;"></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>

            <!--    -->
            <div v-if="state.mynum === 4" class="myinf">
                <div>
                    <label for="title" class="lbl"></label>
                    <p class="question_detail_p">{{ state.onequestionRows.title }}</p>
                </div><br /><br />

                <div>
                    <label for="content" class="lbl"></label>
                    <p class="question_detail_p">{{ state.onequestionRows.content }}</p>
                </div><br />

                <hr />

                <label for="content" class="lbl" style="background-color: skyblue;"></label>
                <div style="width:400px;">
                    <span v-if="state.onequestionreplyRows !== null">
                        {{ state.onequestionreplyRows }}</span>
                    <span v-if="state.onequestionreplyRows === null">  .</span>
                </div>
            </div>
            
            <!--    -->
            <div v-if="state.mynum === 5" class="myinf">
                <table class="table table-hover">
                    <thead>
                        <tr>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(data, i) of state.allmybookmarkrow" :key="i">
                            <th scope="row">{{ data._id }}</th>
                            <td>{{ data.name }}</td>
                            <td>{{ data.address }}</td>
                            <td><el-button @click="movePage(data.bakerynum)"></el-button></td>
                        </tr>
                    </tbody>
                </table>
                <div class="example-pagination-block">
                    <el-pagination layout="prev, pager, next" :total="state.mystartotal" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { reactive, computed, onMounted, ref } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import { watch } from '@vue/runtime-core';

export default {
    setup() {
        const store = useStore();

        const router = useRouter();

        const state = reactive({
            mynum: 1, // 
            files: null,
            image: null,
            postcode: '',
            address: '',
            detailaddress: '',
            password: '',
            password1: '',
            userInfo: computed(() => store.state.userInfo),
            text: '',
            page: 1,
            questionRows: null,
            questionTotal: '',
            onequestionreplyRows: null,
            onequestionRows: null,
            onequestionprev: 0,
            onequestionnext: 0,
            totalshopcount: 0,
            certpercent: 0,
            mytotalreviewcount: 0,
            grade: '',
            allmybookmarkrow: null,
            mystartotal: 0,
        });

        //vuex jwt   user .
        // state.userInfo = computed(() => store.state.userInfo);

        //route      .
        watch(
           () => state.userInfo,
           ()=>{
                imagestate();
                handleQuestion();
                selectshopcount();
                selectmyshopreviewcount();
                selectallmybookmark();
           },
           {deep:true}
        );

        //
        const password = ref();
        const password1 = ref();

        //vuex userInfo   url state.image  onMounted.
        const imagestate = () => {
            if (state.userInfo.imageurl !== null) {
                state.image = state.userInfo.imageurl;
            } else {
                state.image = require('@/assets/imgs/defaultprofile.jpg');
            }

            //      .
            state.address = state.userInfo.address;
            state.detailaddress = state.userInfo.detailaddress;
        };

        //  
        const handleImage = (e) => {
            if (e.target.files.length > 0) {
                state.files = e.target.files[0];
                state.image = URL.createObjectURL(state.files);
            } else {
                state.files = null;
                state.image = require('@/assets/imgs/defaultprofile.jpg');
            }
        };

        // 
        const usermodify = async () => {

            // 
            if (state.password === '') {
                alert(' .');
                password.value.focus();
                return false;
            }
            if (state.password1 === '') {
                alert('  .');
                password1.value.focus();
                return false;
            }
            if (state.password !== state.password1) {
                alert('  .');
                password1.value.focus();
                return false;
            }
            if (state.files === null) {
                alert('  .');
                return false;
            }

            const url = `/api/user/updateuser.json`;
            const headers = { "Content-Type": "multipart-formdata" };
            const body = new FormData();
            body.append("email", state.userInfo.email);
            body.append("password", state.password);
            body.append("address", state.address);
            body.append("detailaddress", state.detailaddress);
            body.append("file", state.files);

            const { data } = await axios.put(url, body, { headers });
            console.log(' ', data);

            if (data.status === 200) {
                alert('.');
                router.go(0); //
            }
        }

        // 
        const sample6_execDaumPostcode = () => {
            console.log('sample6_execDaumPostcode');
            new window.daum.Postcode({
                oncomplete: function (data) {
                    //        .

                    //       .
                    //      ('') ,    .
                    var addr = ''; //  
                    var extraAddr = ''; //  

                    //        .
                    if (data.userSelectedType === 'R') { //     
                        addr = data.roadAddress;
                    } else { //     (J)
                        addr = data.jibunAddress;
                    }

                    //       .
                    if (data.userSelectedType === 'R') {
                        //    . ( )
                        //     "//" .
                        if (data.bname !== '' && /[||]$/g.test(data.bname)) {
                            extraAddr += data.bname;
                        }
                        //  ,   .
                        if (data.buildingName !== '' && data.apartment === 'Y') {
                            extraAddr += (extraAddr !== '' ? ', ' + data.buildingName : data.buildingName);
                        }
                        //    ,     .
                        if (extraAddr !== '') {
                            extraAddr = ' (' + extraAddr + ')';
                        }
                        //     .
                        document.getElementById("sample6_extraAddress").value = extraAddr;

                    } else {
                        document.getElementById("sample6_extraAddress").value = '';
                    }

                    //      .
                    document.getElementById('sample6_postcode').value = data.zonecode;
                    state.postcode = data.zonecode;
                    document.getElementById("sample6_address").value = addr;
                    state.address = addr;
                    //    .
                    document.getElementById("sample6_detailAddress").focus();
                }

            }).open();
        }

        //  
        const handleQuestion = async () => {
            const url = `/api/question/selectmyquestion.json?page=${state.page}&text=${state.text}&writer=${state.userInfo.email}`;
            const headers = { "Content-Type": "application/json" };
            const { data } = await axios.get(url, { headers });
            console.log("  ", data);

            if (data.status === 200) {
                state.questionRows = data.result;
                state.questionTotal = data.total;
            }
        };

        //1  
        const handleOneQuestion = async (num) => {
            const url = `/api/question/selectonequestion.json?_id=${num}`;
            const headers = { "Content-Type": "application/json" };
            const { data } = await axios.get(url, { headers });
            console.log("1   ", data);

            if (data.status === 200) {
                state.onequestionRows = data.result;
                state.onequestionnext = data.next;
                state.onequestionprev = data.prev;
            }
        };

        //
        const selectonequestionrelply = async (num) => {
            const url = `/api/question/selectonequestionrelply.json?origin_id=${num}`;
            const headers = { "Content-Type": "application/json" };
            const { data } = await axios.get(url, { headers });

            if (data.status === 200) {
                state.onequestionreplyRows = data.result.content;
            } else if (data.status === 0) {
                state.onequestionreplyRows = null;
            }
        };

        //    /api/bakery/selectshopcount.json
        const selectshopcount = async () => {
            const url = `/api/bakery/selectshopcount.json`;
            const headers = { "Content-Type": "application/json" };
            const { data } = await axios.get(url, { headers });
            console.log("  ", data);

            if (data.status === 200) {
                state.totalshopcount = data.total;
                state.certpercent = Math.floor((state.userInfo.certreveiwcount / state.totalshopcount) * 100);
                if (state.certpercent < 10) {
                    state.grade = '';
                } else if (state.certpercent >= 10 && state.certpercent < 30) {
                    state.grade = '';
                } else if (state.certpercent >= 30 && state.certpercent < 50) {
                    state.grade = '';
                } else if (state.certpercent >= 50 && state.certpercent < 70) {
                    state.grade = '';
                } else if (state.certpercent >= 70 && state.certpercent < 90) {
                    state.grade = '';
                }
            }
        };

        //    /api/bakery/selectshopcount.json
        const selectmyshopreviewcount = async () => {
            const url = `/api/bakeryreview/selectmyshopreviewcount.json?writer=${state.userInfo.email}`;
            const headers = { "Content-Type": "application/json" };
            const { data } = await axios.get(url, { headers });
            console.log(" ", data);

            if (data.status === 200) {
                state.mytotalreviewcount = data.total;
            }
        };

        //    
        const selectallmybookmark = async () => {
            const url = `/api/mybookmark/selectallmybookmark.json?email=${state.userInfo.email}&page=${state.page}`;
            const headers = { "Content-Type": "application/json" };
            const { data } = await axios.get(url, { headers });
            console.log(" ", data);

            if (data.status === 200) {
                state.allmybookmarkrow = data.result;
                state.mystartotal = data.total;
            }
        };

        const movePage = (_id) => {
            router.push({path:'/bakeryone', query:{_id : _id}});
        }

        //   
        onMounted(() => {
            let script = document.createElement('script');
            script.src = "//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js";
            document.head.appendChild(script);
        });

        return {
            state,
            handleImage,
            sample6_execDaumPostcode,
            usermodify,
            selectonequestionrelply,
            handleOneQuestion,
            password,
            password1,
            movePage,
        }
    }
}
</script>

<style lang="css" scoped>
#mypage_wrap {
    width: 1565px;
    margin: 0 auto;
    overflow: hidden;
    padding-top: 56px;
}

#menu_box {
    background: antiquewhite;
    width: 350px;
    height: 500px;
    float: left;
}

#menu {
    padding: 40px;
    margin-left: 20px;
}

#menu li {
    margin-top: 15px;
    font-weight: bold;
    cursor: pointer;
    font-size: 17px;
    font-family: custom_font2;
}

#content_box {
    float: left;
    margin-left: 100px;
    margin-top: 50px;
}

.myinf_wrap {
    width: 1000px;
}

.myinf_left {
    float: left;
    width: 500px;
}

.myinf_con {}

.myinf_con_p {
    margin-left: 140px;
}

.myinf_lbl {
    width: 120px;
    height: 30px;
    float: left;
    background-color: bisque;
    text-align: center;
    line-height: 30px;
}

.profileImg {
    width: 200px;
}

.lbl {
    width: 120px;
    height: 30px;
    float: left;
    background-color: bisque;
    text-align: center;
    line-height: 30px;
    margin-right: 10px;
}

.question_detail_p {
    float: left;
}
</style>