<template>
    <div id="login_wrap">
        <el-alert v-if="state.loginSuccess==false" id="errorMsg"
            title="error alert"
            type="error"
            :description=state.loginFailMsg
            show-icon
        />
        <br>
        <div>
            <label for="loginEmail" class="lbl">E-mail</label>
            <el-input type="text" v-model="state.form.loginEmail" class="login_input" placeholder=" ." />
        </div>

        <br />
        <br />
        <div>
            <label for="loginPw" class="lbl"></label>
            <el-input type="password" @keyup.enter="handleLogin()" v-model="state.form.loginPw" class="login_input"
                placeholder=" ." />
        </div>
        <br>
        <br>
        <div>
            <label for="button" class="lbl">&nbsp;</label>
            <el-button @click="handleLogin()" id="loginBtn"></el-button>
        </div>
        {{ isLogin }}
        {{ userInfo }}
        <GoogleLogin :callback="callback" id="googlelogin"/>
        <!-- <el-button id="googleLoginBtn"><img src="@/assets/imgs/google.png" id="googleImg"/>Login Using Google</el-button>
            </GoogleLogin> -->
    </div>
</template>

<script>
import axios from 'axios';
import { useStore } from 'vuex';
import { reactive, computed } from 'vue';
import { useRouter } from 'vue-router';
import { decodeCredential } from 'vue3-google-login'

export default {
    setup() {
        const router = useRouter();
        const store = useStore();

        const state = reactive({
            form: {
                loginEmail: "",
                loginPw: "",
            },
            isLogin: "",
            loginSuccess: true,
            loginFailMsg: "",
        });

        state.isLogin = computed(() => store.state.isLogin);
        const userInfo = computed(() => store.state.userInfo);


        //
        const handleLogin = async () => {
            const url = `/api/user/login.json`;
            const headers = { "Content-Type": "application/json" };
            const body = {
                email: state.form.loginEmail,
                password: state.form.loginPw,
            }
            const {data}  = await axios.post(url, body, { headers });
            console.log('', data);

            if (data.status === 200) {
                alert(' ');
                loginIpLogSave(state.form.loginEmail); // ip   
                router.push({ path: '/' });
                store.dispatch("login", data); // vuex login .
            } else {
                state.form.loginPw = "";
                state.loginFailMsg = data.message; //   
                state.loginSuccess = data.loginSuccess;
            }
        }

        // ip   
        const loginIpLogSave = async(email) => {
            const url = `/api/loginiplog/insertloginiplog.json`;
            const headers = { "Content-Type": "application/json" };
            const body = {
                email: email,
            }
            await axios.post(url, body, { headers });
        };

        //  
        const callback = async(response) => {
            const userData = decodeCredential(response.credential);
            console.log("Handle the userData", userData);

            const url = `/api/user/googlelogin.json`;
            const headers = { "Content-Type": "application/json" };
            const body = {
                email: userData.email,
            }
            const {data} = await axios.post(url, body, { headers });
            console.log('db', data);

            if (data.status === 200) {
                alert(' ');
                loginIpLogSave(userData.email); //   ip 
                router.push({ path: '/' });
                store.dispatch("login", data); // vuex login .
            } else {
                if(confirm('   . \'\'   .')){
                    const url = `/api/user/insertuser.json`;
                    const headers = { "Content-Type": "application/json" };
                    const body = {
                        email: userData.email,
                        name: userData.name,
                        password: '123'
                    }

                    const { data } = await axios.post(url, body, { headers });
                    console.log('', data);

                    if (data.status === 200) {
                        alert('.  .');
                        router.push({ path: '/login' });
                    }
                }
                router.push({ path: '/login' });
            }
        }

        return {
            state,
            handleLogin,
            userInfo,
            callback,
        }
    }
}

</script>

<style lang="css" scoped>
#login_wrap {
    width: 1440px;
    margin: 0 auto;
    overflow: hidden;
    padding-left: 450px;
    padding-top: 150px;
    padding-bottom: 200px;
}

.lbl {
    width: 100px;
    float: left;
    text-align: center;
}

.login_input {
    float: left;
    width: 500px;
}

#loginBtn {
    width: 500px;
    font-size: 20px;
    background-color: goldenrod;
    color: white;
}

#googleLoginBtn {
    margin-left: 100px;
    width: 500px;
    margin-top: 20px;
}

#googleImg {
    width: 20px;
    margin-right: 20px;
}

#googlelogin{
    margin-left:100px;
    margin-top:20px;
}

#errorMsg{
    width:600px;
}
</style>