<template>
    <div id="join_wrap">
        <h3></h3>

        <div id="email_box" class="field">
            <label for="" class="lbl" style="float:left;"></label>
            <el-input type="email" ref="email" v-model="state.usermail" style="width:20%;float:left;" placeholder=" " autofocus/>
            <p style="float:left;margin-left:10px;margin-right:10px;"> @ </p>
            <select class="form-select" v-model="state.usermail1" aria-label="Default select example"
                style="width:20%;height:32px;font-size:15px;padding-bottom:1px;padding-top:1px;">
                <option class="options" selected> </option>
                <option class="options" value="naver.com">naver.com</option>
                <option class="options" value="gmail.com">gmail.com</option>
                <option class="options" value="daum.net">daum.net</option>
            </select>
        </div>
        <div style="color:red;font-weight: bold;">{{ state.errorcontent }}</div>
        <div class="field">
            <label for="password" class="lbl"></label>
            <el-input type="password" ref="password" v-model="state.password" style="width:20%;"  placeholder=" "/>
        </div>

        <div class="field">
            <label for="password1" class="lbl"> </label>
            <el-input type="password" ref="password1" v-model="state.password1" style="width:20%;"  placeholder="  "/>
        </div>

        <div class="field">
            <label for="username" class="lbl"></label>
            <el-input type="text" v-model="state.username" ref="name" style="width:20%;"  placeholder=" "/>
        </div>

        <div class="field">
            <label for="address" class="lbl"></label>
            <el-input type="text" id="sample6_postcode" v-model="state.postcode" style="width:200px;" placeholder=""></el-input>
            <el-input type="button" @click="sample6_execDaumPostcode()" value=" " style="width:100px;"></el-input><br>

            <label for="address" class="lbl"></label>
            <el-input type="text" id="sample6_address" ref="address" v-model="state.address" style="width:400px;" placeholder=""></el-input><br>

            <label for="address" class="lbl"></label>
            <el-input type="text" id="sample6_detailAddress" ref="detailaddress" v-model="state.detailaddress" style="width:400px;" placeholder=""></el-input>
            <el-input type="text" id="sample6_extraAddress" style="width:400px;" placeholder=""></el-input>
        </div>

        <div class="field">
            <label for="" class="lbl"></label>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" v-model="state.gender" name="inlineRadioOptions"
                    id="inlineRadio1" value="male">
                <label class="form-check-label" for="inlineRadio1"></label>
            </div>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" v-model="state.gender" name="inlineRadioOptions"
                    id="inlineRadio2" value="female">
                <label class="form-check-label" for="inlineRadio2"></label>
            </div>
        </div>
        
        <div class="field" style="margin-bottom:100px;">
            <label for="" class="lbl"></label>
            <el-button @click="handleJoin()" type="success"></el-button>
        </div>
    </div>
</template>

<script>
import { reactive, ref } from 'vue';
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
import { useRouter } from 'vue-router';

export default {
    setup() {

        const router = useRouter();

        const state = reactive({
            password: '',
            password1: '',
            gender: '',
            usermail: '',
            usermail1: '',
            postcode: '',
            address: '',
            detailaddress: '',
            username: '',
            errorcontent: '',

        });

        const sample6_execDaumPostcode = () => {
            console.log('sample6_execDaumPostcode');
            new window.daum.Postcode({
                oncomplete: function (data) {
                    //        .

                    //       .
                    //      ('') ,    .
                    var addr = ''; //  
                    var extraAddr = ''; //  

                    //        .
                    if (data.userSelectedType === 'R') { //     
                        addr = data.roadAddress;
                    } else { //     (J)
                        addr = data.jibunAddress;
                    }

                    //       .
                    if (data.userSelectedType === 'R') {
                        //    . ( )
                        //     "//" .
                        if (data.bname !== '' && /[||]$/g.test(data.bname)) {
                            extraAddr += data.bname;
                        }
                        //  ,   .
                        if (data.buildingName !== '' && data.apartment === 'Y') {
                            extraAddr += (extraAddr !== '' ? ', ' + data.buildingName : data.buildingName);
                        }
                        //    ,     .
                        if (extraAddr !== '') {
                            extraAddr = ' (' + extraAddr + ')';
                        }
                        //     .
                        document.getElementById("sample6_extraAddress").value = extraAddr;

                    } else {
                        document.getElementById("sample6_extraAddress").value = '';
                    }

                    //      .
                    document.getElementById('sample6_postcode').value = data.zonecode;
                    state.postcode = data.zonecode;
                    document.getElementById("sample6_address").value = addr;
                    state.address = addr;
                    //    .
                    document.getElementById("sample6_detailAddress").focus();
                }

            }).open();
        }

        const name = ref();
        const password = ref();
        const password1 = ref();
        const email = ref();
        const address = ref();
        const detailaddress = ref();

        //
        const handleJoin = async () => {

            if (state.usermail === '') {
                // alert('email .');
                state.errorcontent = "email ";
                email.value.focus();
                return false;
            } else {
                state.errorcontent = '';
            }
            if (state.password === '') {
                alert(' .');
                password.value.focus();
                return false;
            }
            if (state.password1 === '') {
                alert('  .');
                password1.value.focus();
                return false;
            }
            if (state.username === '') {
                alert(' .');
                name.value.focus();
                return false;
            }
            if (state.address === '') {
                alert(' .');
                address.value.focus();
                return false;
            }
            if (state.detailaddress === '') {
                alert(' .');
                detailaddress.value.focus();
                return false;
            }
            if (state.password !== state.password1) {
                alert('  .');
                password1.value.focus();
                return false;
            }

            const url = `/api/user/insertuser.json`;
            const headers = { "Content-Type": "application/json" };
            const body = {
                email: `${state.usermail}@${state.usermail1}`,
                password: state.password,
                name: state.username,
                address: state.address,
                detailaddress: state.detailaddress,
                gender: state.gender,
            }

            const { data } = await axios.post(url, body, { headers });
            console.log('', data);

            if (data.status === 200) {
                alert('.');
                router.push({ path: '/login' });
            }
        };

        //   
        onMounted(() => {
            let script = document.createElement('script');
            script.src = "//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js";
            document.head.appendChild(script);
            console.log(window);
        });

        return {
            state,
            sample6_execDaumPostcode,
            handleJoin,
            password,
            password1,
            email,
            name,
            address,
            detailaddress,
            
            
        }
    }
}
</script>

<style lang="css" scoped>
#join_wrap {
    margin: 0 auto;
    width: 1440px;
    overflow: hidden;
    padding-top: 56px;
}

.field {
    margin-top: 30px;
}

.lbl {
    width: 120px;
    font-weight: bold;
}

#email_box {
    width: 100%;
}
</style>